@import '../../../../resources/sass/variables';
@import 'bootstrap/scss/functions';
@import 'bootstrap/scss/variables';
@import 'bootstrap/scss/mixins';
@import '../../../../resources/sass/mixins/spacings';

.b-article-detail {

	&__head,
	&__content {
		@include make-block-spacing();
	}

	&__head + &__content {
		padding-top: 0;
	}

	&__breadcrumb {
		margin-bottom: 1.5rem;
	}

	&__tags,
	&__footer {
		@include make-block-spacing(('padding'), ('left', 'right'));
		padding-top: 1.5rem;
		padding-bottom: 1.5rem;
		border-top: 1px solid $body-bg;
	}
}

.b-article-detail-head {

	&__title {
		max-width: 41.25rem;
		margin-bottom: 1.5rem;
	}

	&__stickers {
		margin-left: -.63rem;
	}

	&__meta {
		display: flex;
	}

	&__meta-item {
		font-size: $small-font-size;
		color: $extra;
	}

	&__meta-item + &__meta-item {
		padding-left: 1.5rem;
	}
}

.b-article-detail-content {
	&__preview {
		max-width: 38.75rem;
		font-size: 1.25rem;
	}

	img {
		max-width: 100%;
	}

	&__picture {
		@include make-block-spacing-negative(('margin'), ('left', 'right'));
		@include make-block-spacing(('margin'), ('top', 'bottom'));
		text-align: center;
	}
}

hr.title-delimiter {
	display: block;
	width: 6.25rem;
	border-top: .31rem solid theme-color('primary');
	border-radius: .5rem;
	margin: 2rem 0 1.5rem;
}

.article-tag {
	background: $body-bg;
	color: $extra;
	padding: .25rem .5rem;
	font-size: .75rem;
	position: relative;

	@include hover-focus {
		color: $extra;
		text-decoration: none;
	}
}
